.contact{
    padding-top: 50px;
    width: 100%;
    text-align: center;
}
.contact .heading
{
    font-family: Georgia, 'Times New Roman', Times, serif;
  
    font-size: 64px;
    text-align: center;
    padding-top: 25px;
    opacity: 0.1;
}
.contact h2{
    text-align: center;
    font-size: 24px;
    font-weight: bold;
    font-family: math;
    text-align: center;
}
.contactpno
{
    text-align: center;
    width: 40%;
    margin: auto;
    margin-top: 45px;
    padding: 50px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.contactpno p{
    font-size: 18px;
    font-family: math;
    font-weight: 700;
}
.contactpno span
{
    margin-right: 15px;
}
.contact-icons
{
    display: flex;
    gap: 25px;
    justify-content: center;
    margin-top: 25px;
}
.contact-icons svg
{
    border: 1px solid;
    color: #ffb400;
       font-size: 18px;
    padding: 10px;
    border-radius: 25px;
}
.contact-icons svg:hover
{
    transform: translateY(-3px);
}